<template>
  <SimpleModalDlg
    :modalId="modalId"
    title="删除通道"
    :content="`确认删除通道（${cno}）？`"
    defaultButtonText="取消"
    button2Text="确认"
    :button2Action="button2Action" :dialogWidth="300">
  </SimpleModalDlg>
</template>

<script>
import utils from '@/utils/utils'
import SimpleModalDlg from '@/components/utils/SimpleModalDlg'
import apis from '@/apis/apis'

export default {
  name: 'ChannelDeleteDlg',
  components: {
    SimpleModalDlg,
  },
  data () {
    return {
    }
  },
  props: {
    modalId: {
      type: String,
      required: true
    },
    channelId: {
      type: Number,
      required: true
    },
    cno: {
      type: Number,
      required: true
    }
  },
  computed: {
  },
  methods: {
    button2Action () {
      let endpoint = `channel/channels/${this.channelId}/`

      let data = {}

      apis.apiAjaxDelete(endpoint, data,
        () => {
          // if (res.code === 0) {
          alert('通道删除成功成功！')
          this.$emit('channel-deleted')
          // } else {
          //   alert(`通道删除失败(${res.code}): ${res.message}`)
          // }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
  },
  watch: {
  },
  mounted () {
  }
}
</script>
